import { Component, OnInit } from '@angular/core';
import { LocalityService } from 'src/app/shared/locality.service';

@Component({
  selector: 'app-property',
  templateUrl: './property.component.html',
  styleUrls: ['./property.component.scss']
})
export class PropertyComponent implements OnInit {
  prop = {
    mates: '',
    landmark: '',
    starts: 0,
    acc: '',
    ends: 0,
    add: '',
    htype: '',
    state: '',
    country: '',
    pin: ''
  }
  msg: any;
  constructor(private loc: LocalityService) { }

  ngOnInit() {
  }
  props() {
    this.loc.property(this.prop).subscribe(data => {
      if (data['status'] === true) {
        this.eat(data['msg'], 1);
      } else {
        this.eat(data['msg'], 2);
      }
    });
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function () {
      x.className = x.className.replace("show", "");
    }, 5000);
  }


}
