import { Component, OnInit } from '@angular/core';
import { LocalityService } from 'src/app/shared/locality.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-userdata',
  templateUrl: './userdata.component.html',
  styleUrls: ['./userdata.component.scss']
})
export class UserdataComponent implements OnInit {
  msg: any;
  profile: Object;

  constructor(private loc: LocalityService, private router: Router) { }

  ngOnInit() {
    const prof = {
      username: window.localStorage.getItem('logged')
    }
    this.loc.userprofile(prof).subscribe(data => {
      this.profile = data[0];
      // this.eat('Sucess', 1);
    });
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function () {
      x.className = x.className.replace("show", "");
    }, 5000);
  }

}
