import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { LanderComponent } from "src/app/lander/lander.component";
import { ErrorComponent } from "src/app/error/error.component";
import { WaterComponent } from "src/app/water/water.component";
import { LocalityComponent } from "src/app/application/locality/locality.component";
import { ShiftComponent } from "src/app/shift/shift.component";
import { UnshiftComponent } from "src/app/unshift/unshift.component";
import { RentalComponent } from "src/app/rental/rental.component";
import { ProfileComponent } from './application/profile/profile.component';
import { PropertyComponent } from './application/property/property.component';
import { ProfileInfoComponent } from './application/profile-info/profile-info.component';
import { PropertyInfoComponent } from './application/property-info/property-info.component';
import { AuthGuardService } from './guards/auth-guard.service';
import { DisclaimerComponent } from './application/disclaimer/disclaimer.component';
import { UserdataComponent } from './application/userdata/userdata.component';

const routes: Routes = [
  {
    path: "lander",
    component: LanderComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: "",
    redirectTo: "/shift",
    pathMatch: "full"
  },
  {
    path: "water",
    component: WaterComponent
  },
  {
    path: "locality",
    component: LocalityComponent,
    canActivate: [AuthGuardService]
  },  {
    path: "userdata",
    component: UserdataComponent,
    canActivate: [AuthGuardService]
  }, {
    path: "disclaimer",
    component: DisclaimerComponent,
  },
  {
    path: "shift",
    component: ShiftComponent
  },
  {
    path: "unshift",
    component: UnshiftComponent
  },
  {
    path: "rental",
    component: RentalComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: "profile",
    component: ProfileComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: "property",
    component: PropertyComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: "profile-info",
    component: ProfileInfoComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: "property-info",
    component: PropertyInfoComponent,
    canActivate: [AuthGuardService]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
