import { Component, OnInit } from '@angular/core';
import { LocalityService } from 'src/app/shared/locality.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-property-info',
  templateUrl: './property-info.component.html',
  styleUrls: ['./property-info.component.scss']
})
export class PropertyInfoComponent implements OnInit {
  props: any;
  msg: any;

  constructor(private loc: LocalityService, private router: Router) { }

  ngOnInit() {
    this.loc.propertyinfo().subscribe(data => {
      this.props = data;
    });
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function () {
      x.className = x.className.replace("show", "");
    }, 5000);
  }

}
