import { Component, OnInit } from '@angular/core';
import { LocalityService } from 'src/app/shared/locality.service';

@Component({
  selector: 'app-profile-info',
  templateUrl: './profile-info.component.html',
  styleUrls: ['./profile-info.component.scss']
})
export class ProfileInfoComponent implements OnInit {
  msg: any;
  profs: Object;

  constructor(private loc: LocalityService) { }

  ngOnInit() {
    this.loc.profileinfo().subscribe(data => {
      this.profs = data;
    });
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function () {
      x.className = x.className.replace("show", "");
    }, 5000);
  }


}
