import { Component, OnInit } from "@angular/core";
import { LocalityService } from "src/app/shared/locality.service";
import { Router } from "@angular/router";
import { Location } from '@angular/common';

@Component({
  selector: "app-shift",
  templateUrl: "./shift.component.html",
  styleUrls: ["./shift.component.scss"]
})
export class ShiftComponent implements OnInit {
  regData: string;
  reg: boolean;
  logdata: boolean;
  info: any;
  goo = false;
  msg = "";
  enter = true;
  constructor(private loc: LocalityService, private router: Router, private location: Location) { }
  login = {
    username: "",
    password: "",
    cpassword: '',
    mobile: "",
    email: "",
    date : new Date()
  };
  cpassword = "";
  ngOnInit() {
    this.loc.getYellow().subscribe(data => {
      this.info = data;
      // console.log(this.info.length);
    });
  }
  logins() {
    this.enter = false;
    this.logdata = true;
    this.loc.usercheck(this.login).subscribe(ccc => {
      if (ccc['status'] === false) {
        this.loc.login(this.login).subscribe(data => {
          this.logdata = false;
          if (data['status'] === true) {
            console.log(data, data['msg']);
            this.eat(data['msg'], 1);
            this.logdata = false;
            window.localStorage.setItem("logged", this.login.username);
            this.router.navigate(['/userdata']);
            // this.router.navigateByUrl('/profile',{skipLocationChange:true}).then(()=>{
            //   this.router.navigate([decodeURI(this.location.path())]);
            // })
            window.localStorage.setItem("state", "true");
          } else {
            this.eat(data['msg'], 2);
            this.logdata = false;
            this.enter = true;
            window.localStorage.setItem("state", "false");
          }
        });
      }
      else {
        this.logdata = false; this.enter = true;
        this.eat('Username not exists', 2)
      }
    });
  }
  register() {
    this.enter = false;
    // delete this.login.lucky;
    // delete this.login.phrase;
    this.logdata = true;
    this.loc.usercheck(this.login).subscribe(ccc => {
      if (ccc['status'] === true) {
        if (this.login.password === this.login.cpassword) {
          this.loc.register(this.login).subscribe(data => {
            this.logdata = undefined;
            this.enter = true;
            this.reg = true;
            this.regData = data['msg'];
            if (data['status'] === true) {
              this.eat(data['msg'], 1);
            } else {
              this.eat(data['msg'], 2);
            }
          });
        } else {
          this.logdata = undefined;
          this.enter = true;
          this.reg = true;
          this.eat('Password not matched', 2);
        }
      } else {
        this.logdata = undefined;
        this.enter = true;
        this.reg = true;
        this.eat(ccc['msg'], 2);
      }
    });
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function () {
      x.className = x.className.replace("show", "");
    }, 5000);
  }
}
