import { Component, OnInit } from '@angular/core';
import { LocalityService } from 'src/app/shared/locality.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  prof = {
    fname: '',
    lname: '',
    land: 0,
    door: 0,
    loc: '',
    state: '',
    country: '',
    pin: ''
  }
  msg: any;
  eve: any;
  constructor(private loc: LocalityService) { }

  ngOnInit() {
  }
  files(event) {
    console.log(event);
    this.eve = event;
  }
  profile() {
    this.loc.profile(this.prof).subscribe(data => {
      if (data['status'] === true) {
        this.eat(data['msg'], 1);
      } else {
        this.eat(data['msg'], 2);
      }
    });
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function () {
      x.className = x.className.replace("show", "");
    }, 5000);
  }

}
